



















import clickOutside from 'vue-click-outside';
import {
  Component, Model, Prop, Vue,
} from 'vue-property-decorator';
import { IListItem } from '@/types/interface';

@Component({
  directives: {
    clickOutside,
  },
})

export default class InputSelector extends Vue {
  @Prop() txtLabel!: string;
  @Prop() txtPlaceholder!: string;
  @Prop({ default: () => [] }) list!: IListItem[];
  @Model('change', { default: '' }) readonly selected!: string;
  isShow = false;
  selectText = '';

  async selectHandler(opt: IListItem) {
    this.$emit('change', opt.id);
    this.selectText = opt.title;
    this.isShow = false;
  }

  get showText() {
    return this.selected !== '' ? this.selectText : '';
  }

  hide() {
    this.isShow = false;
  }
}
