




























import clickOutside from 'vue-click-outside';
import {
  Component, Model, Prop, Vue,
} from 'vue-property-decorator';
import { IListItem } from '@/types/interface';

@Component({
  directives: {
    clickOutside,
  },
})

export default class InputMultiSelector extends Vue {
  @Prop() txtLabel!: string;
  @Prop() txtPlaceholder!: string;
  @Prop({ default: () => [] }) main!: IListItem[];
  @Prop({ default: () => [] }) sub!: IListItem[];
  @Model('change', { default: '' }) readonly selected!: IListItem[];
  isShow = false;
  isLock = false;

  get selectShow() {
    return this.selected.map((d: IListItem) => d.title).join(',');
  }

  mainHandler(opt: IListItem) {
    this.isLock = true;
    if (!this.selected[0] || opt.id !== this.selected[0].id) this.$emit('change', [opt, '']);
  }

  subHandler(opt: IListItem) {
    this.$emit('change', [this.selected[0], opt]);
    this.isShow = false;
    this.isLock = false;
  }

  clickHandler() {
    if (!this.isLock) this.isShow = !this.isShow;
  }

  hide() {
    this.isShow = false;
    this.isLock = false;
  }
}
