












































































































import {
  Component, Prop, Vue, Watch,
} from 'vue-property-decorator';
import { State, Action } from 'vuex-class';
import {
  IInputInfo, IQuestionItem, IListItem, ITestTemplate, IInformationMessage,
} from '@/types/interface';
import checkID from './checkId';
import checkEmail from './checkEmail';
import * as commonTypes from '../types/common';
import inputCustom from './InputCustom.vue';
import inputSelector from './InputSelector.vue';
import inputTextarea from './InputTextarea.vue';
import inputMultiSelector from './InputMultiSelector.vue';

@Component({
  components: {
    inputCustom,
    inputSelector,
    inputTextarea,
    inputMultiSelector,
  },
})

export default class InputFormPanel extends Vue {
  @State('phone') statePhone!: string;
  @State('snapCode') stateCode!: string;
  @State('name') stateName!: string;
  @State('email') stateEmail!: string;
  @State('pId') statePId!: string;
  @State('address') stateAddress!: string;
  @State('userId') stateUserId!: string;
  @State('ckid') stateCkid !: string;
  @State('errorQuz', { namespace: 'pastTest' }) stateQuz!: IQuestionItem;
  @State('subjectCourseList', { namespace: 'onlineTest' }) stateSubjectCourseList!: IListItem[];
  @State('amountList', { namespace: 'onlineTest' }) stateAmountList!: IListItem[];
  @State('scopeList', { namespace: 'onlineTest' }) stateScopeList!: IListItem[];
  @State('yearList', { namespace: 'pastTest' }) stateYearList!: IListItem[];
  @State('typeList', { namespace: 'pastTest' }) stateTypeList!: IListItem[];
  @State('levelList', { namespace: 'pastTest' }) stateLevelList!: IListItem[];
  @State('subjectList', { namespace: 'pastTest' }) stateSubjectList!: IListItem[];
  @State('courseList', { namespace: 'pastTest' }) stateCourseList!: IListItem[];
  @State('countryList', { namespace: 'testKind' }) stateCountryList!: IListItem[];
  @State('areaList', { namespace: 'testKind' }) stateAreaList!: IListItem[];
  @State('acceptList', { namespace: 'testKind' }) stateAcceptList!: IListItem[];
  @Action('getYearList', { namespace: 'pastTest' }) getYearList!: () => Promise<void>;
  @Action('getTypeList', { namespace: 'pastTest' }) getTypeList!: () => Promise<void>;
  @Action('getLevelList', { namespace: 'pastTest' }) getLevelList!: (params: ITestTemplate | undefined) => Promise<void>;
  @Action('getSubjectList', { namespace: 'pastTest' }) getSubjectList!: (params: ITestTemplate | undefined) => Promise<void>;
  @Action('getCourseList', { namespace: 'pastTest' }) getCourseList!: (params: ITestTemplate | undefined) => Promise<void>;
  @Action('getSubjectCourseList', { namespace: 'onlineTest' }) getSubjectCourseList!: () => Promise<void>;
  @Action('getAmountList', { namespace: 'onlineTest' }) getAmountList!: () => Promise<void>;
  @Action('getScopeList', { namespace: 'onlineTest' }) getScopeList!: () => Promise<void>;
  @Action('getAcceptor', { namespace: 'testKind' }) getAcceptor!: () => void;
  @Action('getCounty', { namespace: 'testKind' }) getCounty!: () => void;
  @Action('getArea', { namespace: 'testKind' }) getArea!: (id: string) => void;
  @Prop() inputs!: string[];
  @Prop() error!: string;
  @Prop({ default: false }) isCheckOn!: boolean;

  id = '';
  name = '';
  phone = '';
  email = '';
  password = '';
  newPassword = '';
  captcha = '';
  ckPassword = '';
  txtErrorList: string[] = [];
  address: IListItem[] = [];
  ckClass = '';
  ckConsult = '';
  ckid = '';
  isCoolDown = false;
  snapCoolDown = 40;
  areaBase = {};
  year = '';
  yearList: IListItem[] = [];
  testType = '';
  typeList: IListItem[] = [];
  testLevel = '';
  levelList: IListItem[] = [];
  testSubject = '';
  subjectList: IListItem[] = [];
  testCourse = '';
  courseList: IListItem[] = [];
  testSubjectCourse = '';
  subjectCourseList: IListItem[] = [];
  testAmount = '';
  amountList: IListItem[] = [];
  testScope = '';
  scopeList: IListItem[] = [];
  question = '';
  fullAddress = '';
  passwordFlag = true;
  newPasswordFlag = true;
  ckPasswordFlag = true;
  isMobileDisabled = false;
  isCkidDisabled = false;

  private types = commonTypes;
  private errorList: string[] = [];

  created() {
    this.phone = this.statePhone;
    this.captcha = this.stateCode;
    this.name = this.stateName;
    this.email = this.stateEmail;
    this.fullAddress = this.stateAddress;
    this.id = this.statePId;
    this.ckid = this.stateCkid;
    this.question = `${this.stateQuz.qNum},${this.stateQuz.id}`;
    if (this.inputs.indexOf(this.types.INPUT_CK_TEST_SUBJECT_COURSE) >= 0) {
      this.getSubjectCourseList()
        .then(() => {
          this.subjectCourseList = this.stateSubjectCourseList;
        });
    }

    if (this.inputs.indexOf(this.types.INPUT_CK_TEST_AMOUNT) >= 0) {
      this.getAmountList()
        .then(() => {
          this.amountList = this.stateAmountList;
        });
    }

    if (this.inputs.indexOf(this.types.INPUT_CK_TEST_SCOPE) >= 0) {
      this.getScopeList()
        .then(() => {
          this.scopeList = this.stateScopeList;
        });
    }

    if (this.inputs.indexOf(this.types.INPUT_CK_YEAR_LIST) >= 0) {
      this.getYearList()
        .then(() => {
          this.yearList = this.stateYearList;
        });
    }

    if (this.inputs.indexOf(this.types.INPUT_CK_TEST_TYPE) >= 0) {
      this.getTypeList()
        .then(() => {
          this.typeList = this.stateTypeList;
        });
    }

    if (this.inputs.indexOf(this.types.INPUT_CK_CLASS) >= 0) {
      this.getAcceptor();
    }

    if (this.inputs.indexOf(this.types.INPUT_ADDRESS) >= 0) {
      this.getCounty();
    }

    this.isMobileDisabled = this.isCheckOn && this.statePhone !== '';
    this.isCkidDisabled = this.isCheckOn && this.stateCkid !== '';
  }

  private showInput(item: string) {
    return this.inputs.length === 0 || this.inputs.indexOf(item) >= 0;
  }

  private errorClass(item: string) {
    return this.errorList.indexOf(item) >= 0 ? 'error' : '';
  }

  public getInput(): IInputInfo | undefined {
    this.checkInput(undefined);
    let tempId;
    if (this.inputs.indexOf(this.types.INPUT_ID) >= 0) tempId = this.id;
    if (this.inputs.indexOf(this.types.INPUT_CK_ID) >= 0) tempId = this.ckid;

    return this.txtErrorList.length === 0 ? {
      Source: 'CF05',
      Mobile: this.inputs.indexOf(this.types.INPUT_PHONE) >= 0 ? this.phone : undefined,
      SnapCode: this.inputs.indexOf(this.types.INPUT_CAPTCHA) >= 0 ? this.captcha : undefined,
      Pa5wpd: this.inputs.indexOf(this.types.INPUT_PASSWORD) >= 0 ? this.password : undefined,
      NewPa5wpd: this.inputs.indexOf(this.types.INPUT_NEW_PASSWORD) >= 0 ? this.newPassword : undefined,
      ID: tempId,
      Email: this.inputs.indexOf(this.types.INPUT_EMAIL) >= 0 ? this.email : undefined,
      address: this.inputs.indexOf(this.types.INPUT_FULL_ADDRESS) >= 0 ? this.fullAddress : undefined,
      Name: this.inputs.indexOf(this.types.INPUT_NAME) >= 0 ? this.name : undefined,
      RealName: this.inputs.indexOf(this.types.INPUT_NAME) >= 0 ? this.name : undefined,
    } : undefined;
  }

  public getPasswordInput(): IInputInfo | undefined {
    this.checkInput(this.password === '' && this.newPassword === '' ? [] : undefined);
    return this.txtErrorList.length === 0 ? {
      Source: 'CF05',
      UserGuid: this.stateUserId,
      OldPa5wpd: this.inputs.indexOf(this.types.INPUT_PASSWORD) >= 0 ? this.password : undefined,
      Pa5wpd: this.inputs.indexOf(this.types.INPUT_NEW_PASSWORD) >= 0 ? this.newPassword : undefined,
    } : undefined;
  }

  public getTestCondition(): ITestTemplate | undefined {
    this.checkInput(undefined);
    return this.txtErrorList.length === 0 ? {
      year: this.year,
      type: this.testType,
      level: this.testLevel,
      subject: this.testSubject,
      course: this.testCourse,
    } : undefined;
  }

  public getTestBaseCondition(): ITestTemplate | undefined {
    this.checkInput(undefined);
    return this.txtErrorList.length === 0 ? {
      course: this.inputs.indexOf(this.types.INPUT_CK_TEST_SUBJECT_COURSE) >= 0 ? this.testSubjectCourse : undefined,
      year: this.inputs.indexOf(this.types.INPUT_CK_TEST_SCOPE) >= 0 ? this.testScope : undefined,
      amount: this.inputs.indexOf(this.types.INPUT_CK_TEST_AMOUNT) >= 0 ? this.testAmount : undefined,
    } : undefined;
  }

  public getTestAdvCondition(): ITestTemplate | undefined {
    this.checkInput([]);
    return this.txtErrorList.length === 0 ? {
      type: (this.inputs.indexOf(this.types.INPUT_CK_TEST_TYPE) >= 0 && this.testType !== '') ? this.testType : undefined,
      subject: (this.inputs.indexOf(this.types.INPUT_CK_TEST_SUBJECT) >= 0 && this.testSubject !== '') ? this.testSubject : undefined,
      level: (this.inputs.indexOf(this.types.INPUT_CK_TEST_LEVEL) >= 0 && this.testLevel !== '') ? this.testLevel : undefined,
    } : undefined;
  }

  public getInformation(): IInformationMessage | undefined {
    this.checkInput(undefined);
    return this.txtErrorList.length === 0 ? {
      Email: this.inputs.indexOf(this.types.INPUT_EMAIL) >= 0 ? this.email : undefined,
      Name: this.inputs.indexOf(this.types.INPUT_NAME) >= 0 ? this.name : undefined,
      Mobile: this.inputs.indexOf(this.types.INPUT_PHONE) >= 0 ? this.phone : undefined,
      postCode: (this.inputs.indexOf(this.types.INPUT_ADDRESS) >= 0 && this.address.length === 2) ? this.address[1].id : undefined,
      region: (this.inputs.indexOf(this.types.INPUT_ADDRESS) >= 0 && this.address.length === 2) ? this.address[1].title : undefined,
      city: (this.inputs.indexOf(this.types.INPUT_ADDRESS) >= 0 && this.address.length === 2) ? this.address[0].title : undefined,
      class: (this.inputs.indexOf(this.types.INPUT_CK_CLASS) >= 0 && this.ckClass !== '') ? this.ckClass : undefined,
      consult: (this.inputs.indexOf(this.types.INPUT_CK_CONSULT) >= 0 && this.ckConsult !== '') ? this.ckConsult : undefined,
    } : undefined;
  }

  public resetSelection() {
    this.testType = '';
    this.testLevel = '';
    this.testSubject = '';
    this.testSubjectCourse = '';
    this.testScope = '';
    this.testAmount = '';
  }

  public resetPassword() {
    this.password = '';
    this.newPassword = '';
    this.captcha = '';
    this.errorList = [];
    this.txtErrorList = [];
  }

  public resetInformation() {
    this.address = [];
    this.ckClass = '';
    this.ckConsult = '';
  }

  public getNewPassword(): IInputInfo | undefined {
    this.checkInput([this.types.INPUT_NEW_PASSWORD]);
    return this.txtErrorList.length === 0 ? {
      Source: 'CF05',
      Mobile: this.phone,
      SnapCode: this.captcha,
      Pa5wpd: this.newPassword,
    } : undefined;
  }

  private sendSnapCode() {
    if (this.isCoolDown) return;
    this.checkInput([this.types.INPUT_PHONE]);
    if (this.txtErrorList.length === 0) {
      this.isCoolDown = true;
      this.captcha = '';
      this.timedCount();
      this.$emit('send', this.phone);
    }
  }

  timeOutFunc = 0;
  private timedCount() {
    if (this.snapCoolDown === 0) {
      this.isCoolDown = false;
      this.snapCoolDown = 40;
      return;
    }
    this.snapCoolDown -= 1;
    this.timeOutFunc = setTimeout(this.timedCount, 1000);
  }

  private checkInput(list: string[] | undefined) {
    const inputs = list || this.inputs;
    this.errorList = [];
    this.txtErrorList = [];
    let checkEmpty = false;
    let checkFormat = false;
    inputs.forEach((item: string) => {
      if (!this.inputCheckEmpty(item)) {
        checkEmpty = true;
        this.errorList.push(item);
      }
      if (!this.inputCheckFormat(item)) {
        checkFormat = true;
        if (this.errorList.indexOf(item) < 0) this.errorList.push(item);
      }
    });
    if (checkEmpty) this.txtErrorList.push('欄位為必填');
    if (checkFormat) this.txtErrorList.push('格式錯誤');
  }

  private inputCheckEmpty(item: string) {
    switch (item) {
      case this.types.INPUT_ID:
        return this.id !== '';
      case this.types.INPUT_NAME:
        return this.name !== '';
      case this.types.INPUT_PHONE:
        return this.phone !== '';
      case this.types.INPUT_EMAIL:
        return this.email !== '';
      case this.types.INPUT_PASSWORD:
        return this.password !== '';
      case this.types.INPUT_NEW_PASSWORD:
        return this.newPassword !== '';
      case this.types.INPUT_CAPTCHA:
        return this.captcha !== '';
      case this.types.INPUT_CK_PASSWORD:
        return this.ckPassword !== '';
      case this.types.INPUT_FULL_ADDRESS:
        return this.fullAddress !== '';
      case this.types.INPUT_ADDRESS:
        return this.address.length === 2;
      case this.types.INPUT_CK_YEAR_LIST:
        return this.year !== '';
      case this.types.INPUT_CK_TEST_TYPE:
        return this.testType !== '';
      case this.types.INPUT_CK_TEST_LEVEL:
        return this.testLevel !== '';
      case this.types.INPUT_CK_TEST_SUBJECT:
        return this.testSubject !== '';
      case this.types.INPUT_CK_TEST_COURSE:
        return this.testCourse !== '';
      case this.types.INPUT_CK_TEST_SUBJECT_COURSE:
        return this.testSubjectCourse !== '';
      case this.types.INPUT_CK_TEST_AMOUNT:
        return this.testAmount !== '';
      case this.types.INPUT_CK_TEST_SCOPE:
        return this.testScope !== '';
      case this.types.INPUT_CK_QUZ_ID:
        return this.question !== '';
      case this.types.INPUT_CK_CONSULT:
        return this.ckConsult !== '';
      case this.types.INPUT_CK_CLASS:
        return this.ckClass !== '';
      case this.types.INPUT_CK_ID:
        return this.ckid !== '';
      default:
        return false;
    }
  }

  private inputCheckFormat(item: string) {
    switch (item) {
      case this.types.INPUT_PHONE:
        return this.phone === '' || RegExp('^09[0-9]{8}$').test(this.phone);
      case this.types.INPUT_EMAIL:
        return this.email === '' || checkEmail(this.email);
      case this.types.INPUT_ID:
        return this.id === '' || checkID(this.id);
      default:
        return true;
    }
  }

  @Watch('testType')
  onChangeTestType(nVal: string) {
    if (nVal !== '' && this.inputs.indexOf(this.types.INPUT_CK_TEST_LEVEL) >= 0) {
      this.levelList = [];
      this.testLevel = '';
      this.subjectList = [];
      this.testSubject = '';
      this.courseList = [];
      this.testCourse = '';
      this.getLevelList({ type: nVal })
        .then(() => {
          this.levelList = this.stateLevelList;
        });
    }
  }

  @Watch('testLevel')
  onChangeTestLevel(nVal: string) {
    if (nVal !== '' && this.inputs.indexOf(this.types.INPUT_CK_TEST_SUBJECT) >= 0) {
      this.subjectList = [];
      this.testSubject = '';
      this.courseList = [];
      this.testCourse = '';
      this.getSubjectList({ type: this.testType, level: nVal })
        .then(() => {
          this.subjectList = this.stateSubjectList;
        });
    }
  }

  @Watch('testSubject')
  onChangeTestSubject(nVal: string) {
    if (nVal !== '' && this.inputs.indexOf(this.types.INPUT_CK_TEST_COURSE) >= 0) {
      this.courseList = [];
      this.testCourse = '';
      this.getCourseList({ type: this.testType, level: this.testLevel, subject: nVal })
        .then(() => {
          this.courseList = this.stateCourseList;
        });
    }
  }

  @Watch('address')
  onChangeAddress(nVal: IListItem[], oVal: IListItem[]) {
    if (nVal[0] && oVal[0]) {
      if (nVal[0].id !== oVal[0].id) {
        this.getArea(nVal[0].id);
      }
    }
    if (nVal[0] && !oVal[0]) this.getArea(nVal[0].id);
  }

  checkForId() {
    return this.isCheckOn && this.id !== '' && checkID(this.id);
  }

  checkForName() {
    return this.isCheckOn && this.name !== '';
  }

  checkForEmail() {
    return this.isCheckOn && this.email !== '' && checkEmail(this.email);
  }

  checkForFullAddress() {
    return this.isCheckOn && this.fullAddress !== '';
  }
}
